import { Fragment } from "react";
import { Dialog, Transition } from "@headlessui/react";
import { XIcon } from "@heroicons/react/outline";
import { useSelector, useDispatch } from "react-redux";
import { cartValues } from "../../assets/constants";
import { deleteCart } from "../../Axios/Requests/Cart";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

export default function CheckoutDrawer() {
  const cart = useSelector((state) => state.users);
  const userId = cart.userObj._id;
  const dispatch = useDispatch();
  const hideModel = () => {
    dispatch({
      type: "CART_DRAWER",
      payload: { drawer: false, cartData: cart.cartData },
    });
  };
  const cartData = cart.cartData;
  const notify = () => {
    // Calling toast method by passing string
    toast.error("Product Removed", {
      position: "top-center",
      autoClose: 3000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };
  const UndoValue = (i) => {
    console.log("Undo Value", i);

    deleteCart(i, userId)
      .then((res) => {
        dispatch({
          type: "CART_DRAWER",
          payload: { cartData: res.data, drawer: true },
        });
        console.log("Hello", res);
        notify();
      })
      .catch((err) => {
        console.log(err);
      });
  };
  if (cartData) {
    var sum = 0;
    for (var i = 0; i < cartData.length; i++) {
      sum += cartData.products[i].qty * cartData.products[i].qty;
    }
  }
  const checkCartData = cartData.products ? cartData.products : [];

  return (
    <>
      <Transition.Root show={cart.cartDrawer} as={Fragment}>
        <Dialog as="div" className="relative z-10" onClose={() => hideModel()}>
          <Transition.Child
            as={Fragment}
            enter="ease-in-out duration-500"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in-out duration-500"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
          </Transition.Child>

          <div className="fixed inset-0 overflow-hidden">
            <div className="absolute inset-0 overflow-hidden">
              <div className="pointer-events-none fixed inset-y-0 right-0 flex max-w-full pl-10">
                <Transition.Child
                  as={Fragment}
                  enter="transform transition ease-in-out duration-500 sm:duration-700"
                  enterFrom="translate-x-full"
                  enterTo="translate-x-0"
                  leave="transform transition ease-in-out duration-500 sm:duration-700"
                  leaveFrom="translate-x-0"
                  leaveTo="translate-x-full"
                >
                  <Dialog.Panel className="pointer-events-auto w-screen max-w-md">
                    <div className="flex h-full flex-col overflow-y-scroll bg-white shadow-xl">
                      <div className="flex-1 overflow-y-auto py-6 px-4 sm:px-6">
                        <div className="flex items-start justify-between">
                          <Dialog.Title className="text-lg font-medium text-gray-900">
                            {" "}
                            Shopping cart{" "}
                          </Dialog.Title>
                          <div className="ml-3 flex h-7 items-center">
                            <button
                              type="button"
                              className="-m-2 p-2 text-gray-400 hover:text-gray-500"
                              onClick={() => hideModel()}
                            >
                              <span className="sr-only">Close panel</span>
                              <XIcon className="h-6 w-6" aria-hidden="true" />
                            </button>
                          </div>
                        </div>

                        <div className="mt-8">
                          <div className="flow-root">
                            <ul
                              role="list"
                              className="-my-6 divide-y divide-gray-200"
                            >
                              {checkCartData.map((item, index) => (
                                <li key={index} className="flex py-6">
                                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                    <img
                                      src={`https://hackaton-final.herokuapp.com/${item.product.img}`}
                                      alt={item.product.name}
                                      className="h-full w-full object-cover object-center"
                                    />
                                  </div>

                                  <div className="ml-4 flex flex-1 flex-col">
                                    <div>
                                      <div className="flex justify-between text-base font-medium text-gray-900">
                                        <h3>
                                          <a href="#"> {item.product.name} </a>
                                        </h3>
                                        <p className="ml-4">
                                          {item.product.price}$
                                        </p>
                                      </div>
                                      {/* <p className="mt-1 text-sm text-gray-500">{product.color}</p> */}
                                    </div>
                                    <div className="flex flex-1 items-end justify-between text-sm">
                                      <p className="text-gray-500">
                                        Qty {item.qty}
                                      </p>

                                      <div className="flex">
                                        <button
                                          onClick={() => UndoValue(item._id)}
                                          type="button"
                                          className="font-medium text-indigo-600 hover:text-indigo-500"
                                        >
                                          Remove
                                        </button>
                                      </div>
                                    </div>
                                  </div>
                                </li>
                              ))}
                            </ul>
                          </div>
                        </div>
                      </div>

                      <div className="border-t border-gray-200 py-6 px-4 sm:px-6">
                        <div className="flex justify-between text-base font-medium text-gray-900">
                          <p>Subtotal</p>
                          <p>${sum}.00</p>
                        </div>
                        <p className="mt-0.5 text-sm text-gray-500">
                          Shipping and taxes calculated at checkout.
                        </p>
                        <div className="mt-6">
                          <Link to={"/checkout"} onClick={() => hideModel()}>
                            <a
                              href="/"
                              className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                            >
                              Checkout
                            </a>
                          </Link>
                        </div>
                        <div
                          className="mt
                      -6 flex justify-center text-center text-sm text-gray-500"
                        >
                          <p>
                            or{" "}
                            <Link to={"/"}>
                              <button
                                type="button"
                                className="font-medium text-indigo-600 hover:text-indigo-500"
                                onClick={() => hideModel()}
                              >
                                Continue Shopping
                                <span aria-hidden="true"> &rarr;</span>
                              </button>
                            </Link>
                          </p>
                        </div>
                      </div>
                    </div>
                  </Dialog.Panel>
                </Transition.Child>
              </div>
            </div>
          </div>
        </Dialog>
      </Transition.Root>
      <ToastContainer />
    </>
  );
}
